/* Make the card take full width on small screens and limit width on larger screens */
.question-card {
    width: 90%; /* Take almost full width on small screens */
    max-width: 700px; /* Prevent it from being too wide on large screens */
    margin: auto;
    align-items: center; /* Centers spinner horizontally */
    justify-content: center;
    padding: 16px;
    display: flex;
    flex-direction: column;
    gap: 10px; /* Space between elements */
  }
  
  /* Ensure the form field takes full width */
  .full-width {
    width: 100%;
  }
  
  /* Responsive button container */
  .button-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  /* Responsive adjustments */
  @media (max-width: 600px) {
    .question-card {
      width: 100%; /* Full width on very small screens */
      padding: 12px;
    }
  }
  
  /* Center the spinner */
  .spinner-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%; /* Full height of the card */
    position: absolute;
    width: 100%;
  }
  